.homeCards {
    margin: 20px 40px 20px 20px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 20px;
  }
  
  .homeCards img {
    width: 100%;
    margin-bottom: 20px;
  }
  
  .homeCards h3 {
    margin-bottom: 5px;
  }
  
  .homeCards a {
    display: inline-block;
    padding-top: 10px;
    color: #0067b8;
    text-transform: uppercase;
    font-weight: bold;
  }
  
  .homeCards a:hover i {
    margin-left: 10px;
  }